import React, { useEffect } from 'react'
import { useHistory } from 'react-router-dom'

import Grid from '@material-ui/core/Grid'

import GameTile from './GameTile'

import './Home.css'

function Home () {
    const history = useHistory()

    const handleGameTileClick = (value) => {
        console.log('gameTileClick(home): ' + value)

        history.push({
            pathname: '/app/level',
            search: '?category=' + value,
            state: { category: value }
        })
    }

    useEffect(() => {
        localStorage.removeItem('wordsSolvedArray')
        localStorage.removeItem('currentWordTextFieldsSolved')
    });
    
    return (
        <div className='home'>
            <div className='game-body'>
                <Grid style={{marginTop:0}} container direction='row' justify='center' alignItems='center' spacing={3}>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'fairy-tale', title:'אַגָּדוֹת', imageurl: process.env.PUBLIC_URL + '/fairy-tale/frog.svg' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'love', title:'אַהֲבָה', imageurl: process.env.PUBLIC_URL + '/love/heart.svg' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'food', title:'אֹכֶל', imageurl: process.env.PUBLIC_URL + '/food/cherry.svg' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'home', title:'בַּבַּיִת', imageurl: process.env.PUBLIC_URL + '/home/image.svg' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                </Grid>
                <Grid style={{marginTop:12}} container direction='row' justify='center' alignItems='center' spacing={3}>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'animals', title:'בַּעֲלֵי חַיִּים', imageurl: process.env.PUBLIC_URL + '/animals/giraffe.svg' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'fruits-and-vegetables', title:'יְרָקוֹת וּפֵרוֹת', imageurl: process.env.PUBLIC_URL + '/fruits-and-vegetables/027-strawberry.svg' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'tools', title:'כֵּלִים', imageurl: process.env.PUBLIC_URL +'/tools/hammer.svg' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'science', title:'מַדָּע וְחָלָל', imageurl: process.env.PUBLIC_URL + '/science/planet-earth.svg' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                </Grid>
                <Grid style={{marginTop:12}} container direction='row' justify='center' alignItems='center' spacing={3}>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'kitchen', title:'מִטְבָּח', imageurl: process.env.PUBLIC_URL + '/kitchen/oven-mitts.svg' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'devices', title:'מַכְשִׁירִים', imageurl: process.env.PUBLIC_URL + '/devices/desk-lamp.svg' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'medicine', title:'רְפוּאָה', imageurl: process.env.PUBLIC_URL + '/medicine/hospital.svg' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'transport', title:'תַּחְבּוּרָה', imageurl: process.env.PUBLIC_URL + '/transport/aeroplane.svg' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                </Grid>
            </div>
        </div>
    )
}

export default Home
